.scoreboard-ledwall{display:flex;flex-direction:column;align-items:center;background-color:#000;color:#fff;padding:20px;border-radius:8px;box-shadow:0 0 10px rgba(0,0,0,.2);width:100%;height:100vh;margin:0;text-align:center;position:relative}.scoreboard-ledwall .match-info{margin-bottom:20px}.scoreboard-ledwall .match-info h1{font-size:4rem;font-weight:bold;margin:0}.scoreboard-ledwall .match-info h2{font-size:1.5rem;margin-top:5px;color:#ccc}.scoreboard-ledwall .score-section{display:flex;align-items:center;justify-content:space-around;width:100%;height:50%}.scoreboard-ledwall .score-section .team{display:flex;flex-direction:column;align-items:center;width:50%}.scoreboard-ledwall .score-section .team .logo-score{display:flex;align-items:center;justify-content:flex-start;gap:10px}.scoreboard-ledwall .score-section .team .team-logo{width:200px;height:200px}.scoreboard-ledwall .score-section .team .team-score{font-size:10rem;color:#fff;font-weight:bold;-webkit-text-stroke:3px #ffed00;text-shadow:1px 1px 2px rgba(0,0,0,.7)}.scoreboard-ledwall .score-section .team .team-name-prima{font-size:2.2rem;font-weight:bold;color:#ccc;margin-top:10px;max-width:200px;text-align:center;white-space:normal;margin-left:8%}.scoreboard-ledwall .score-section .team .team-name-seconda{font-size:2.2rem;font-weight:bold;color:#ccc;margin-top:10px;max-width:200px;text-align:center;white-space:normal;margin-right:13%}.scoreboard-ledwall .score-section .score{display:flex;align-items:center;gap:15px}.scoreboard-ledwall .score-section .score .score-value{font-size:14rem;font-weight:bold;color:#ffed00;-webkit-text-stroke:3px #fff;text-shadow:1px 1px 2px rgba(0,0,0,.7)}.scoreboard-ledwall .score-section .score .versus{font-size:5rem;color:#fff}.scoreboard-ledwall .stripes-section{display:flex;justify-content:space-between;width:80%;margin-top:20px;margin-bottom:50px}.scoreboard-ledwall .stripes-section .stripe{flex:1;height:50px;margin:0 5px;position:relative}.scoreboard-ledwall .stripes-section 