.scoreboard-diretta {
  background-color: #008000;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  width: 1280px; /* Larghezza fissa */
  height: 720px; /* Altezza fissa */
  margin: 0 auto; /* Centra orizzontalmente */
  text-align: center;
  position: relative;
  overflow: hidden; /* Evita che gli elementi escano dai confini */
}

.diretta {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute; /* Cambiato da fixed a absolute per adattarsi al contenitore */
  background-color: #f8f9fa;
  padding: 20px;
  border: 2px solid #343a40;
  border-radius: 8px;
  max-width: 569px;
  width: 569px; /* Mantieni la dimensione del box */
  justify-content: space-between;
  top: 10px; /* Posizionamento relativo al contenitore */
  right: 10px;
}

.teams-container {
  display: flex;
  flex-direction: column;
  margin-right: 10px;
  transform: scale(0.9); /* Riduzione generale delle dimensioni */
  transform-origin: top left; /* Mantiene il punto di riferimento */
}

.team {
  display: flex;
  align-items: center;
  width: 100%;
}

.team-logo-container {
  display: flex;
  align-items: center;
}

.team-logo {
  width: 8.5% !important;
  height: 16.5% !important;
}


.color-strip {
  width: 8px; /* Ridotto leggermente per proporzione */
  height: 50px; /* Ridotto leggermente */
  margin-right: 8px;

  .home {
    background-color: #8a2be2 !important;
  }
  
  .away {
    background-color: #ffd700 !important;
  }
}

.team-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 60%; /* Ridotto per coerenza */
  font-size: 85%; /* Leggera riduzione del testo */
}

.team-name-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  min-width: 180px; /* Riduzione minima della larghezza */
  margin-left: 0;
}

.team-name {
  font-size: 98%;
  color: #343a40;
  margin-left: 15px;
  font-weight: bold;
  
}

