.carousel-container-dir {
  position: absolute;
  bottom: 10px;
  left: 20px;
  max-width: 180px;
  max-height: 180px;
  overflow: hidden;
  z-index: 10;
  top: 15px;
  margin-left: 25%;
}

.carousel-scroll-vert {
  display: flex;
  white-space: nowrap;
}

.carousel-item {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  width: 100%;
  height: 100%;
  max-width: 300px;
  max-height: 300px;
}

.carousel-item img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.logo-container-diretta {
  position: absolute; /* Allinea il logo rispetto al contenitore */
  top: 3px; /* Posiziona in alto */
  left: 20px; /* Posiziona a sinistra */
  z-index: 10;
}

.logo-container-diretta {
  position: relative;
