  font-size: 11px; /* Dimensione dell'icona */
  display: inline-block;
  margin-left: 10px;
  top: 0; /* Allineamento verticale */
} 

.set-icon-container {
 /*  margin-left: 5px; /* Distanza tra il punteggio e l'icona 
